import React, {Component} from "react";
import {connect} from "react-redux";
import {OraButton} from "eshop/components/OraCommonComponents";
import LabeledInput from "../../core/components/hoc/LabeledInput";

class SingleInputModalContent extends Component {
    onButtonClick(event) {
        this.props.onButtonClick(event);
    }

    setInputValue({id, name, value}) {
        this.props.setInputValue({id, name, value});
    }

    getPropsForInput() {
        return {
            id: this.props.id,
            name: this.props.id,
            type: "text",
            labelType: "floating",
            label: this.props.inputPlaceHolder,
            mask: this.props.mask,
            placeholder: this.props.placeholder,
            value: this.props.inputValue || "",
            errors: !!this.props.error && Object.keys(this.props.error).length > 0 ? [this.props.error] : [],
            autoComplete: false,
            errorWrapperClassName: "u-spacing-l",
            onChange: this.setInputValue.bind(this),
            valid: !!this.props.error && Object.keys(this.props.error).length > 0 ? false : undefined,
            validateEmpty: false,
            showMaskOnHover: true
        };
    }

    render() {
        return (
            <div>
                <h1 className="u-font-standard u-no-margin u-padding-l">
                    <span className="h3">{this.props.modalHeader}</span>
                </h1>
                <p className="u-padding-l">{this.props.modalMainText}</p>
                <LabeledInput {...this.getPropsForInput()}/>
                <OraButton type="primary" size="medium" onClick={this.onButtonClick.bind(this)} className="u-w-100">
                    {this.props.modalBtnText}
                </OraButton>
            </div>
        );
    }
}

export default SingleInputModalContent;